.row{display: flex;}
.c1{
    display:inline-block;
    width: 19.5734%;
}
.c2{display:inline-block;
    width: 80.4166%; 
}

@media only screen and (max-width: 390px) {
    .c1{
        display:none;
    }
    .c2{
        width: 100%; 
    }

    .c1_open{
        display:inline-block;
        width:100%;
        position:fixed;
        top:0;
        left:100;
        background-color: rgb(255,255,255,0.8);
        z-index: 10;


    }

}

@media only screen and (max-width: 768px) and (min-width:391px){
   .c1{
       width:100%;
   }
   .c2{
       width:100%;
   }
   .row{
       flex-flow:row wrap;
   }
}