.swiper{
    width:100%;;
    height: 24vw;

}
.logo{
    width:2vw;
    height: 2vw;
}
.arrows{
    margin-bottom: 2vw;
    color:#A7A7A7;
    font-size: 2vw;
}
.left{
    margin-right: 1.5vw;
    cursor: pointer;
}
.right{
    margin-left:1.5vw;
    cursor: pointer;
}

@media only screen and (max-width: 390px) {
    .swiper{
        width:150%;;
        height: 68vw;;
        margin-left:200vw;
    
    }
      
}

@media only screen and (max-width: 768px) and (min-width:391px){
    .swiper{
        width:100%;;
        height: 44vw;;
        margin-left:200vw;
    
    }
}