.dash{
    overflow-x: hidden;
}
.dashbooard{
    font-family: var(--font_2);
    margin-top:4.479vw;
    text-align: left;
    color:var(--color_37);
}
.menu_item1{
    margin-top:1.442vw;
    text-align: left;
    display: flex;
    margin-left:2.467vw;
    vertical-align: middle;
}
.icon1{
    height: 2.604vw;
    width: 2.604vw;
    margin-right:0.692vw;
}
.menu_text1{
    color:var(--color_43);
    vertical-align: middle;
    margin-top:auto;
    margin-bottom: auto;

}

.menu_item2{
    margin-top:1.442vw;
    text-align: right;

    display: flex;
   margin-left:2vw;
    vertical-align: middle;
    font-family: var(--font_1);
   

}
.menu_item3{
    margin-top:1.12vw;
    text-align: right;
    display: flex;
   margin-left:2vw;
    vertical-align: middle;
    font-family: var(--font_1); 
}
.icon2{
    height: 2.082vw;
    width: 2.082vw;
    margin-right:0.692vw;
}
.menu_text2{
    color:var(--color_43);
    vertical-align: middle;
    margin-top:auto;
    margin-bottom: auto;
    font-family: var(--font_1);

}

.hr{
    border-top: 1px solid var(--color_39);
    width:81.26vw;
    margin-top:1.04vw;
    margin-top:2.5635vw;
}
.row{display: flex;
}
.c1{
    display:inline-block;
    width: 15%;
}
.c2{display:inline-block;
    width: 15%; 
    
}
.c3{
    display:inline-block;
    width: 70% 
}
.menuImg{
    display:none;
}
.select{
    width:14.481vw;
    height:2.5vw;
    background-color: white;
    color:#737679;
    border:none;
    scrollbar-color: yellow blue;
}
.option{
    height:2.5vw;
    background-color: white;
    color:#737679;

}
@media only screen and (max-width: 390px) {
    
    .c3{
        display: none;
    }
    .c1{
        width:50%;
    }
    .c2{
        width:50%;
    }

    .dashbooard_s{
        margin-top:8.205vw;
        text-align: center;
    }
    .menuImg{
display: inline-block;
position:absolute;
top:5.128vw;
left:2.307vw;
color:var(--color_51);
font-size: 6vw;;
    }

    .icon1{
        height: 10.83vw;
        width: 10.83vw;
        margin-right:2.866vw;
        margin-left:8vw;
    }

    .icon2{
        height: 7.72vw;
        width: 7.72vw;
        margin-right:2.866vw;
    }

    .menu_text2{
       padding-right:15vw;
    
    }
    .hr{
        visibility: hidden;
    }
    

}

@media only screen and (max-width: 768px) and (min-width:391px){
  
    .dashbooard{
        display: none;
    }
    .hr{
        display:none;
    }

    .row{
        justify-content: center;
    }

    .menu_item1{
        margin-top:3.214vw;
        margin-left:auto;
        justify-content: flex-end;
        }
    .icon1{
        height: 4.427vw;
        width: 4.427vw;
        margin-right:1.190vw;
    }
    
    .menu_item2{
justify-content: flex-end;    
        margin-left:4.855vw;
        margin-top:3.214vw;

    }
    .icon2{
        height: 3.802vw;
        width: 3.802vw;
        margin-right:1.876vw;
    }
    .c3{
        width:60%;
    }
}
